import React, { Component } from 'react'
import { Flex, WingBlank, WhiteSpace, NavBar, Toast } from 'antd-mobile'
import { setToken, ZFW_TOKEN } from '../../utils/index'
import { Link } from 'react-router-dom'
import { login } from '../../utils/api/user'
import styles from './index.module.css'
import { withFormik } from 'formik'
// import { BASE_URL } from '../../utils/axios'
import * as yup from 'yup'
// 
// const REG_UNAME = /^[a-zA-Z_\d]{5,8}$/
// const REG_PWD = /^[a-zA-Z_\d]{5,12}$/

class Login extends Component {
  // 
  state = {
    username: '',
    password: ''
  }

  // 
  // handlerChange = (e) => {
  //   console.log(e)
  //   this.setState({
  //     // key:value
  //     // [e.target.name] 
  //     [e.target.name]: e.target.value
  //   })
  // }
  // 
  // loginForm = async (e) => {
  //   // 
  //   e.preventDefault()
  //   const { username, password } = this.state
  //   // 
  //   const res = await login({ username, password })
  //   const { status, data } = res
  //   if (status === 200) {
  //     Toast.success('', 2)
  //     setSession(ZFW_TOKEN, data.token)
  //     this.props.history.push('/')
  //   } else {
  //     Toast.fail('', 2)
  //   }
  // }


  render() {
    const {
      values,
      touched,
      errors,
      handleChange,
      handleBlur,
      handleSubmit,
    } = this.props
    return (
      <div className={styles.root}>
        {/*  */}
        <NavBar mode="light">
          
        </NavBar>
        <WhiteSpace size="xl" />

        {/*  */}
        <WingBlank>
          <form onSubmit={handleSubmit}>
            <div className={styles.formItem}>
              <input
                value={values.name}
                className={styles.input}
                name="username"
                placeholder=""
                onChange={handleChange}
                onBlur={handleBlur}
              />
              {errors.name && touched.name && <div id="feedback">{errors.name}</div>}
            </div>
            {/* 58 */}
            {/* <div className={styles.error}></div> */}
            <div className={styles.formItem}>
              <input
                className={styles.input}
                name="password"
                type="password"
                value={values.password}
                onBlur={handleBlur}
                onChange={handleChange}
                placeholder=""
              />
              {errors.password && <div className={styles.error}>{errors.password}</div>}
            </div>
            {/* 512 */}
            {/* <div className={styles.error}></div> */}
            <div className={styles.formSubmit}>
              <button className={styles.submit} type="submit">
                 
              </button>
            </div>
          </form>
          <Flex className={styles.backHome}>
            <Flex.Item>
              <Link to="/registe">~</Link>
            </Flex.Item>
          </Flex>
        </WingBlank>
      </div>
    )
  }
}

export default withFormik({

  // 1. 2. inputname
  mapPropsToValues: () => ({ username: '', password: '' }),
  // 
  validationSchema: yup.object().shape({
    username: yup.string().required('').matches(/^[a-z0-9_-]{5,8}$/, '58'),
    password: yup.string().required('').matches(/^[a-z0-9_-]{5,12}$/, '512'),
  }),
  // 
  handleSubmit: async (values, formikBag) => {
    const { username, password } = values
    const res = await login({
      username,
      password
    })
    console.log(res)
    const {status, data, description } = res
    if (status === 200) {
      
      setToken(data.token)
      Toast.success(description, 2)
      // 
      // if( formikBag.props.location.data && formikBag.props.location.data.backUrl) {
      // 
      if( formikBag.props.location.data?.backUrl) {
        formikBag.props.history.push(formikBag.props.location.data.backUrl)
      }else { 
        formikBag.props.history.push('/home/profile')

      }
    } else {
      Toast.fail(description)
    }
  }
})(Login)
